export var weeks = [
    {
        iden: '0',
        week: ''
    },
    {
        iden: '1',
        week: ''
    },
    {
        iden: '2',
        week: ''
    },
    {
        iden: '3',
        week: ''
    },
    {
        iden: '4',
        week: ''
    },
    {
        iden: '5',
        week: ''
    },
    {
        iden: '6',
        week: ''
    }
];
var weekMaps = new Map();
weeks.forEach(function (item) {
    weekMaps.set(item.iden, item.week);
});
export { weekMaps };
